<template>
  <div class="protocol-container" v-show="protocolShow">
    <div class="protocol-modal">
      <div class="protocol-title relative"><i class="fa fa-window-close-o protocol-close" @click="protocolShow = false"></i></div>
      <div class="protocol-body text-lg">
        <p><span class="text-dark"></span><span class="text-dark text-underline"></span></p>
        <p class="text-dark m-t-md">1</p>
        <p class="text-dark m-t-md">2</p>
        <p class="text-dark m-t-md">3</p>
        <p class="m-t-md"></p>
        <p class="m-t-md"><span class="text-dark"></span> <span class="text-blue1"></span></p>
        <p class="text-underline text-dark m-t-md"></p>
        <p class="text-blue1 m-t-md"></p>
        <p class="text-blue1 m-t-md"></p>
        <p class="text-blue1 m-t-md"></p>
      </div>
      <button class="protocol-btn block" @click="acceptProtocol"></button>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      value: false
    },
    data: () => ({
      data: '',
      protocolShow: false
    }),
    methods: {
      acceptProtocol () {
        this.$emit('protocol')
        this.protocolShow = false
      }
    },
    mounted () {
      this.protocolShow = this.value
    },
    watch: {
      value (val) {
        this.protocolShow = val
      },
      protocolShow (val) {
        this.$emit('input', val)
      }
    }
  }
</script>
<style>
  .protocol-container {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.5);
    z-index: 9;
  }
  .protocol-modal {
    width: 872px;
    background: rgba(255,255,255,1);
    border-radius:2px;
    box-shadow:0px 2px 8px rgba(165,165,165,0.75);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /*padding: 50px 47px 37px;*/
  }
  .protocol-title {
    background-color: #222D32;
    height:66px;
    font-size:20px;
    font-family:MicrosoftYaHei;
    color:rgba(231,231,231,1);
    line-height:66px;
    padding-left: 38px;
  }
  .protocol-close {
    position: absolute;
    right: 18px;
    top: 16px;
    color: #FFFFFF;
    cursor: pointer;
    padding: 6px 12px;
  }
  .protocol-body {
    padding: 36px 36px 0;
  }
  .protocol-btn {
    margin: 20px auto;
    width: 257px;
    height: 50px;
    line-height: 50px;
    border-radius: 4px;
    background-color: #3691F5;
    color: #fff;
    font-size: 20px;
    text-align: center;
  }
  .protocol-body {
    color: #808080;
  }
</style>



