<template>
    <!---->
    <div v-loading="loading">
        <div class="p-lg appli-container">
            <sub-header :list="subNavList"></sub-header>
            <div class="page-container">
                <div class="page-toolbar clear m-t-sm">
                    <search-ipts :options="searchOptions" @submit="doSearch" v-show="searchShow"></search-ipts>
                    <div class="pull-left toolbar-candle clear">
                        <a href="javascript:;" title="" @click="addItem"
                           class="app-add btn bg-blue1 text-white"><i class="fa fa-plus-square"></i>
                        </a>
                        <a href="javascript:;" title="" class="app-add btn bg-red1 text-white"
                           @click="delItem(selectedGroup)"><i
                            class="fa fa-trash"></i></a>
                        <!-- <div class="app-del btn bg-red1 text-white"><i class="fa fa-minus-square"></i></div> -->
                        <div class="app-refresh btn bg-gray1" title="" @click="refresh"><i
                            class="fa fa-refresh"></i></div>
                    </div>
                    <div class="pull-right clear">
                        <div class="pull-left m-r-sm opacity-8" title="">
                            <select-checkbox :list="showList" v-model="selectVal" style="width: 60px;"></select-checkbox>
                        </div>
                        <div class="pull-left btn opacity-8 search-btn" @click="searchShow = !searchShow"><i
                            class="fa fa-search" title=""></i></div>
                    </div>
                </div>
                <div class="page-contaoner">
                    <div class="lk-table m-t-sm">
                        <ul class="table-thead clear">
                            <li class="col-xs-24 p-n select-box">
                                <el-checkbox v-model="selectAll"></el-checkbox>
                            </li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('ID')!=-1">ID</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1"></li>
                        </ul>
                        <ul class="table-tbody clear" v-for="(item, index) in data.items" @click="selectItem(item.id)">
                            <li class="col-xs-24 p-n select-box">
                                <el-checkbox :label="item.id" v-model="selectedGroup"></el-checkbox>
                            </li>
                            <li class="col-xs-24 p-n" v-show="selectVal.indexOf('ID')!=-1" :title="item.id">{{item.id}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.username">{{item.username}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.nickname">{{item.nickname}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.name">{{item.name}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.email">{{item.email}}</li>
                            <li class="col-xs-1 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.mobile">{{item.mobile}}</li>
                            <li class="col-xs-24 p-n over-omit" v-show="selectVal.indexOf('')!=-1" :title="item.status_name" :class="item.status==2?'text-red':'text-green'">{{item.status_name}}</li>
                            <li class="col-xs-1 p-n" v-show="selectVal.indexOf('')!=-1">
                                <!--<a href="javascript:;" title="" class="candle-btn btn" @click.stop="openDetail(item)"><i-->
                                    <!--class="fa fa-search-plus"></i></a>-->
                                <a href="javascript:;" title="" class="candle-btn btn" @click.stop="editItem(item)"><i
                                    class="fa fa-edit"></i></a>
                                <a href="javascript:;" :title="item.status==2?'':''" class="candle-btn btn" @click.stop="statusChange(item)"><i
                                    class="fa" :class="item.status==2? 'fa-lightbulb-o' : 'fa-ban'"></i></a>
                                <a href="javascript:;" title="" class="candle-btn btn"
                                   @click.stop="delItem([item.id])"><i class="fa fa-trash"></i></a>
                            </li>
                        </ul>
                        <ul class="table-tbody clear" v-if="data.items.length===0">
                            <li class="p-n over-omit"></li>
                        </ul>
                    </div>
                    <div class="footer clear m-t-md">
                        <div class="pull-left news-record text-lg">
                            <span v-show="data.items.length>0">{{offset + 1}}{{offset + data.items.length}}</span><span
                            class="text-blue">{{data.total}}</span> 
                            <el-select v-model="limit" placeholder="" style="width: 66px;">
                                <el-option
                                    v-for="(item,index) in options"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                            
                        </div>
                        <div class="pull-right">
                            <el-pagination
                                background
                                layout="prev, pager, next"
                                :current-page.sync="page"
                                :page-count="pages">
                            </el-pagination>
                        </div>
                    </div>
                    <el-dialog
                        title=""
                        :visible.sync="detailModal"
                        custom-class="dialog-modal1">
                        <div v-loading="modalLoading">
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right text-bold min-width-105"></div>
                                <div class="col-xs-8 line-height-40 text-bold"></div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.username}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.nickname}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.groupname}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">Email:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.email}}</div>
                            </div>
                            <div class="clear bg-f9">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.mobile}}</div>
                            </div>
                            <div class="clear">
                                <div class="col-xs-2 line-height-40 text-right min-width-105">:</div>
                                <div class="col-xs-8 line-height-40">{{detailVal.status}}</div>
                            </div>
                        </div>
                    </el-dialog>
                    <el-dialog
                        :title="type=='edit'?'':''"
                        :visible.sync="editModal"
                        custom-class="dialog-modal1"
                        :close-on-click-modal="false">
                        <div v-loading="modalLoading">
                            <div class="clear m-b-sm">
                                <div class="col-xs-12 col-sm-2 line-height-40 text-right min-width-105"><span class="text-red">*</span>:</div>
                                <div class="col-xs-12 col-sm-8 team-select">
                                    <el-select :class="{'border-red': groupError}" @change="groupChange" v-model="detailVal.group_id" placeholder="" popper-class="select-team">
                                        <el-option
                                            v-for="(item, index) in teamOptions"
                                            :key="index"
                                            :label="item.name"
                                            :value="item.id">
                                        </el-option>
                                    </el-select>
                                    <p v-if="groupError" class="text-red"><span class="fa fa-close m-r-xs"></span></p>
                                </div>
                            </div>
                            <div class="clear m-b-sm">
                                <div class="col-xs-12 col-md-2 line-height-40 text-right min-width-105"><span class="text-red">*</span>:</div>
                                <div class="col-xs-12 col-md-8">
                                    <el-input placeholder="" :class="{'border-red': usernameError}" v-model="detailVal.username" @blur="validateUsername(detailVal.username)"></el-input>
                                    <p v-if="usernameError" class="text-red"><span class="fa fa-close m-r-xs"></span>3-12</p>
                                </div>
                            </div>
                            <div class="clear m-b-sm">
                                <div class="col-xs-12 col-md-2 line-height-40 text-right min-width-105"><span class="text-red">*</span>:</div>
                                <div class="col-xs-12 col-md-8">
                                    <el-input placeholder="" :class="{'border-red': nicknameError}" v-model="detailVal.nickname" @blur="validateNickname(detailVal.nickname)"></el-input>
                                    <p v-if="nicknameError" class="text-red"><span class="fa fa-close m-r-xs"></span></p>
                                </div>
                            </div>
                            <div class="clear m-b-sm">
                                <div class="col-xs-12 col-md-2 line-height-40 text-right min-width-105"><span class="text-red">*</span>:</div>
                                <div class="col-xs-12 col-md-8">
                                    <el-input placeholder="" :class="{'border-red': mobileError}" v-model="detailVal.mobile" @blur="validateMobile(detailVal.mobile)"></el-input>
                                    <p v-if="mobileError" class="text-red"><span class="fa fa-close m-r-xs"></span></p>
                                </div>
                            </div>
                            <div class="clear m-b-sm">
                                <div class="col-xs-12 col-md-2 line-height-40 text-right min-width-105"><span class="text-red">*</span>Email:</div>
                                <div class="col-xs-12 col-md-8">
                                    <el-input placeholder="Email" :class="{'border-red': emailError}" v-model="detailVal.email" @blur="validateEmail(detailVal.email)"></el-input>
                                    <p v-if="emailError" class="text-red"><span class="fa fa-close m-r-xs"></span></p>
                                </div>
                            </div>
                            <div class="clear m-b-sm">
                                <div class="col-xs-12 col-md-2 line-height-40 text-right min-width-105"><span class="text-red">*</span>:</div>
                                <div class="col-xs-12 col-md-8">
                                    <el-input placeholder="" type="password" :class="{'border-red': passwordError}" v-model="detailVal.password" @blur="validatePsd(detailVal.password)"></el-input>
                                    <p v-if="passwordError" class="text-red"><span class="fa fa-close m-r-xs"></span>6-20,</p>
                                </div>
                            </div>
                            <div class="clear m-b-sm" v-if="type=='add'">
                                <div class="col-xs-12 col-md-2 line-height-40 text-right min-width-105"><span class="text-red">*</span>:</div>
                                <div class="col-xs-12 col-md-8 line-height-40">
                                    <el-radio :label="1" v-model="detailVal.status"></el-radio>
                                    <el-radio :label="2" v-model="detailVal.status"></el-radio>
                                </div>
                            </div>
                            <div class="text-center m-t-lg clear">
                                <div class="col-xs-12 col-md-9">
                                    <el-button type="primary" @click="dailogSubmit"> </el-button>
                                    <el-button @click="editModal = false"> </el-button>
                                </div>
                            </div>
                        </div>
                    </el-dialog>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import SubHeader from '../common/subheader'
    import api from '@/api'
    import format from '@/tools/format'
    import validate from '@/tools/validate'
    import SelectCheckbox from '@/components/SelectCheckbox'
    import SearchIpts from '../common/searchIpts'
    export default {
        //
        components: {
            SelectCheckbox,
            SearchIpts,
            SubHeader,
        },
        data: () => ({
            data: {
                limit: 10,
                page: 1,
                pageSize: 1,
                total: 1,
                items: []
            },
            teamOptions: [],
            loading: false,
            modalLoading: false,
            selectVal: ['ID', '', '', '', '', '', '', ''],
            showList: ['ID', '', '', '', '', '', '', ''],
            options: [10, 25, 50],   //
            searchShow: false,   //
            selectAll: false,    //
            selectedGroup: [], // 
            limit: 10,
            page: 1,
            //
            detailVal:{
                group_id: '',
                username:'',
                nickname:'',
                mobile: '',
                email:'',
                password: '',
                status: 1
            }, // 
            groupError: false,
            usernameError: false,
            nicknameError: false,
            emailError: false,
            mobileError: false,
            passwordError: false,
            id: '',
            type: '',
            detailModal: false, // 
            editModal: false, //
            //
            subNavList: {
                parentNode: {
                    name: '',
                    router: {
                        name: 'account_list'
                    }
                },
                childNode: {
                    name: '',
                    router: {
                        name: 'account_list'
                    }
                }
            },
            //
            searchOptions: [
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'text',
                    name: '',
                    value: ''
                },
                {
                    type: 'select',
                    name: '',
                    value: '',
                    options: [
                        {
                            value: 1,
                            label: ''
                        },
                        {
                            value: 2,
                            label: ''
                        }
                    ]
                }
            ],
        }),
        computed: {
            //
            pages () {
                return this.data.pageSize || 1
            },
            offset () {
                return (this.page - 1) * this.limit
            }
        },
        methods: {
            //
            getList () {
                this.loading = true
                this.$http.post(api.account.list, {
                    username: this.searchOptions[0].value,
                    email: this.searchOptions[1].value,
                    status: this.searchOptions[2].value,
                    page: this.page,
                    limit: this.limit
                }).then(res => {
                    this.selectedGroup = []
                    let that = this
                    setTimeout(function () {
                        that.loading = false
                    }, 500)
                    if (res.data.code === 1) {
                        this.data = res.data.data
                    } else {
                        this.data = {
                            limit: 10,
                            page: 1,
                            pageSize: 1,
                            total: 1,
                            items: []
                        }
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            // 
            getGroupList () {
                this.$http.get(api.account.childGroup).then(res => {
                    if (res.data.code === 1) {
                        this.teamOptions = res.data.data.items
                        if (this.type === 'edit') {
                            this.teamOptions.map(val => {
                                if (val.name === this.detailVal.name) {
                                    this.$set(this.detailVal, 'group_id', val.id)
                                }
                            })
                        }
                    } else {
                        this.teamOptions = []
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            //
            delItem(id) {
                if (id.length > 0) {
                    this.$confirm(id.length > 1 ? ', ?' : ', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.$http.get(api.account.delete, {
                            params: {
                                id: id.join(',')
                            }
                        }).then(res => {
                            if (res.data.code === 1) {
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                })
                                this.getList()
                            } else {
                                this.$message({
                                    type: 'warning',
                                    message: res.data.msg
                                })
                            }
                        })
                    }).catch(() => {
                    })
                } else {
                    this.$message({
                        type: 'warning',
                        message: ''
                    })
                }
            },
            // 
            addItem () {
                this.type = 'add'
                this.detailVal = {
                    username: '',
                    nickname: '',
                    mobile: '',
                    email: '',
                    group_id: '',
                    password: '',
                    status: 1
                }
                this.editModal = true
                this.getGroupList()
            },
            // 
            getDetail (item) {
                this.modalLoading = true
                this.$http.get(api.account.detail, {
                    params: {
                        id: item.id
                    }
                }).then(res => {
                    if (res.data.code === 1) {
                        let that = this
                        setTimeout(function () {
                            that.modalLoading = false
                        }, 500)
                        this.detailVal = res.data.data
                        if (this.type === 'edit') {
                            this.getGroupList()
                        }
                    } else {
                        this.$message({
                            type: 'warning',
                            message: res.data.msg
                        })
                    }
                })
            },
            // 
            openDetail (item) {
                this.detailModal = true
                this.getDetail(item)
            },
            //
            editItem (item) {
                this.type = 'edit'
                this.editModal = true
                this.getDetail(item)
            },
            statusChange (item) {
                this.$confirm(item.status === 2 ? ', ?' : ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$http.get(api.account.disable, {
                        params: {
                            id: item.id,
                            status: item.status === 2 ? 1 : 2
                        }
                    }).then(res => {
                        if(res.data.code === 1) {
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                            if (item.status === 2) {
                                item.status_name = ''
                                item.status = 1
                            } else {
                                item.status = 2
                                item.status_name = ''
                            }
                        } else {
                            this.$message({
                                type: 'error',
                                message: res.data.msg
                            })
                        }
                    })
                })
            },
            //
            dailogSubmit () {
                if (!this.detailVal.group_id) this.groupError = true
                this.validateUsername(this.detailVal.username)
                this.validateNickname(this.detailVal.nickname)
                this.validateEmail(this.detailVal.email)
                this.validateMobile(this.detailVal.mobile)
                this.validatePsd(this.detailVal.password)
                if (this.groupError || this.usernameError || this.nicknameError || this.emailError || this.mobileError || this.passwordError) {
                    this.$message({
                        type: 'warning',
                        message: ''
                    })
                    return
                }
                if (this.type === 'edit') {
                    this.$http.post(api.account.edit, this.detailVal).then(res => {
                        if (res.data.code === 1) {
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                            this.editModal = false
                            this.getList()
                        } else {
                            this.$message({
                                type: 'warning',
                                message: res.data.msg
                            })
                        }
                    })
                } else {
                    this.$http.post(api.account.add, this.detailVal).then(res => {
                        if (res.data.code === 1) {
                            this.$message({
                                type: 'success',
                                message: ''
                            })
                            this.editModal = false
                            this.getList()
                        } else {
                            this.$message({
                                type: 'warning',
                                message: res.data.msg
                            })
                        }
                    })
                }
            },
            // 
            validateUsername (val) {
                this.usernameError = this.vUsername(val)
            },
            validateNickname (val) {
                this.nicknameError = val ? false : true
            },
            validateEmail (val) {
                this.emailError = this.vEmail(val)
            },
            validateMobile (val) {
                this.mobileError = this.vMobile(val)
            },
            validatePsd (val) {
                if (this.type === 'add' || this.detailVal.password) this.passwordError = this.vPassword(val)
                if (this.type === 'edit' && !this.detailVal.password) this.passwordError = false
            },
            vUsername: validate.username,
            vEmail: validate.email,
            vMobile: validate.mobile,
            vPassword: validate.password,
            groupChange () {
                this.groupError = false
            },
            //
            refresh () {
                this.getList()    //
            },
            // 
            doSearch (data) {
                this.page = 1
                this.searchOptions = data
                this.getList()
            },
            // 
            selectItem (id) {
                if (this.selectedGroup.indexOf(id) !== -1) {
                    this.selectedGroup.splice(this.selectedGroup.indexOf(id), 1)
                } else {
                    this.selectedGroup.push(id)
                }
            },
            // 
            addPage () {
                if (this.page < this.pages) this.page += 1
            },
            // 
            delPage () {
                if (this.page > 1) this.page -= 1
            },
            format: format
        },
        created () {
            this.page = this.$route.query.page ? parseInt(this.$route.query.page) : 1
            this.getList()
        },
        watch: {
            //checkbook
            selectedGroup (val) {
                if (val.length === this.data.items.length && val.length > 0) {    //
                    this.selectAll = true
                } else {
                    this.selectAll = false
                }
            },
            //
            selectAll (val) {
                if (val) {
                    this.selectedGroup = []
                    this.data.items.map(val => {
                        this.selectedGroup.push(val.id)
                    })
                } else {
                    if (this.selectedGroup.length !== this.data.items.length - 1) {
                        this.selectedGroup = []
                    }
                }
            },
            page (val) {
                this.$router.replace({name: 'account_list', query: {page: val}})
                this.getList()
            },
            limit (val) {
                this.getList()
            },
            editModal (val) {
                if (val) {
                    this.groupError = false
                    this.usernameError = false
                    this.nicknameError = false
                    this.emailError = false
                    this.mobileError = false
                    this.passwordError = false
                }
            }
        }
    }
</script>
<style>

</style>




